@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');
* {
    margin: 0;
    padding: 0;
}

body {
    padding: 20px;
    font-family: "Poppins";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    box-sizing: border-box;
    outline: none;
    background-color: rgb(30, 30, 30);
    color: white;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
    width: 100%;
    max-width: 1200px;
}

a {
    color: rgb(255, 255, 255);
}

p {
    margin: 20px 0;
}

button {
    border: 0;
    padding: 8px;
    border-radius: 4px;
    color: white;
    background: var(--primary);
    cursor: pointer;
}

header nav {
    display: flex;
    gap: 16px;
    justify-content: end;
    max-width: 1200px;
    margin: 0 auto;
}

header nav h1 {
    margin-right: auto;
    border-bottom: 3px solid rgb(218, 34, 255);
}

header nav a {
    text-decoration: none;
    padding: 6px;
    border-radius: 4px;
}

a.active {
    background: rgb(218, 34, 255);
    transition: .2s ease-in-out;
}

main {
    max-width: 1200px;
    margin: 40px auto;
}

.help-layout nav {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin: 30px 0;
}

.help-layout nav a {
    padding: 10px;
    border: 2px solid white;
    border-radius: 4px;
    text-decoration: none;
}

.help-layout nav a:hover {
    border-color: rgb(218, 34, 255);
}


/* faq */

.faq .question {
    background: rgba(0, 0, 0, 0.2);
    padding: 5px 20px;
    border-radius: 4px;
    margin: 20px 0;
}


/* contact form */

form {
    margin-top: 30px;
}

form input,
form label span,
form textarea {
    display: block;
}

form input,
form textarea {
    margin-bottom: 30px;
    padding: 8px;
    border-radius: 4px;
    border: 0;
    width: 300px;
    color: var(--secondary);
}

form label span {
    margin-bottom: 10px;
}


/* careers list */

.careers>a {
    display: block;
    background: rgba(0, 0, 0, 0.2);
    padding: 20px;
    border-radius: 4px;
    margin: 20px 0;
    text-decoration: none;
}

.careers>a:hover p {
    color: rgb(218, 34, 255);
    transition: .2s ease-in-out;
}

.careers p {
    margin: 0;
}

.careers>a p:last-child {
    font-size: 0.8em;
    color: #bbb;
}


/* careers details */

.career-details {
    background: rgba(0, 0, 0, 0.2);
    padding: 20px;
    border-radius: 4px;
    margin: 20px 0;
}


/* breadcrumbs */

.breadcrumbs {
    max-width: 1200px;
    margin: 20px auto;
}

.breadcrumbs>* {
    display: inline-block;
    margin-right: 10px;
}

.breadcrumbs .crumb:after {
    content: '>';
    margin-left: 10px;
}

.breadcrumbs .crumb:last-child:after {
    display: none;
}

.breadcrumbs .crumb a {
    color: #ddd;
}